import React from 'react'; import styled from 'styled-components';
// =---------------=---------------------------------------------------------------------=:>
import Greting from '~Client/Containers/Content/AboutMe/Components/Greeting';
import AboutMeNavContent from '~Client/Containers/Content/AboutMe/Components/AboutMeNavContent';
import Game from '~Client/Containers/Content/AboutMe/Components/Game';
// =======================================================================================>
const AboutMe = (props) => (
    <Wrapper>
        <Greting />
        <AboutMeNavContent />
        <Game />
    </Wrapper>
)

const Wrapper = styled.div `width: 100%; overflow: auto; border-radius: 60px 125px 60px 60px; font-family: Caveat;
    box-shadow: 0 0 8px 0px #000; border: 8px solid rgba(255,255,255,.5);
    background: linear-gradient(60deg, #f79533, #f37055, #ef4e7b, #a166ab, #5073b8, #1098ad, #07b39b, #6fba82);`



export default AboutMe

